
import { NgModule } from '@angular/core'
import { RouterModule } from '@angular/router'
import { PageComponent } from './page/page.component'
import { LoginComponent } from '../login/login.component'
import { HomeComponent } from '../pages/home/components/home.component';
// import { NzLayoutModule } from 'ng-zorro-antd/layout';
import {  NzDropDownModule, NzIconModule, NzLayoutModule, NzMenuModule, NzBreadCrumbModule } from 'ng-zorro-antd';
@NgModule({
    declarations:[PageComponent],
    imports: [
        NzBreadCrumbModule,
        NzLayoutModule,
        NzMenuModule,
        NzIconModule,
        NzDropDownModule,
        RouterModule.forChild([
            { path: 'login', component: LoginComponent },
            { 
                path: 'page', 
                component: PageComponent ,
                children: [
                        { path: 'home', loadChildren : () => import('./home/home.module').then(x => x.HomeModule)},
                        { path: 'product', loadChildren: () => import('./product/product.module').then(x => x.ProductModule)}
                ]
            }
        ])
    ]
})

export class PageModule { }